//Footer
.titles-footer {
    color: $color-complementar-principal;
    font-family: Inter;
    font-size: 14px;
    font-weight: 500;
    line-height: 120%;
}

.link-footer {
    color: #FFF;
    font-family: Inter;
    font-size: 14px;
    font-weight: 300;
    line-height: 140%;
    &.link-maps {
        transition: all 0.3s ease-in;
        &:hover {
            color: $color-complementar-principal;
        }
    }
}

.link-botao-contato {
    width: 100%;
    max-width: 19rem;
    transition: all 0.6s ease;
    &:hover {
        max-width: 22rem;
    }
}