.s-entre-contato {
    background-color: $color-complementar-principal;
    padding-top: 48px;
    padding-bottom: 48px;
    .container {
        display: flex;
        justify-content: space-between;
        align-items: center;
            .text-entre-contato {
                width: 100%;
                color: #FFF;
                font-size: 24px;
                font-weight: 700;
                line-height: 112%; /* 26.88px */
                .span-text-primary {
                    color: $bg-primary;  
                    }
            }
            .botoes-contato {
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: end;
                gap: 31px;
            }
    }

    @include responsive(600px) {
        .container {
            flex-direction: column;
            text-align: center;
            .text-entre-contato {
                margin-bottom: 3rem;
            }
            .botoes-contato {
                justify-content: center;
            }
        }
    }
    @include responsive(420px) {
        .container {
            flex-direction: column;
            text-align: center;
            .text-entre-contato {
                font-size: 20px;
            }
            .botoes-contato {
                gap: 15px;
                .btn-trabalhe-conosco {
                    max-width: 100%;
                    padding: 15px;
                    p {
                        max-width: 100%;
                        font-size: 13px;
                        margin-right: 5px;
                    }
                }
                .btn-fale-conosco {
                    max-width: 100%;
                    padding: 15px;
                    p {
                        max-width: 100%;
                        font-size: 13px;
                        margin-right: 5px;
                    }
                }
            }
        }
    }
}

.s-sustentabilidade {
    width: 100%;
    padding-top: 13rem;
    padding-bottom: 15.4rem;
    background-image: url(./img/bg-sustentabilidade.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    .container {
        .text {
            display: flex;
            flex-direction: column;
            justify-content: start;
            width: 100%;
            max-width: 51%;
            h3 {
                margin-top: 1.2rem;
                margin-bottom: 2.4rem;
            }
            .span-title {
                color: $color-green;
            }
        }
    }

    @include responsive(1150px) {
        .container {
            .text {
                .span-subtitulo {
                    font-size: 12px;
                }
                h3 {
                    font-size: 28px;
                }
                p {
                    font-size: 15px;
                }
            }
        }
    }
    @include responsive(900px) {
       .container {
        .text {
            max-width: 60%;
            h3 {
                font-size: 26px;
            }
        }
       }
    }
    @include responsive(800px) {
        .container {
         .text {
            text-align: center;
             max-width: 100%;
             h3 {
                 font-size: 26px;
             }
         }
        }
     }
}

.s-unidades {
    padding-top: 9.3rem;
    padding-bottom: 9.3rem;
    overflow: hidden;
    .container {
        display: flex;
        .left {
            width: 100%;
            max-width: 36.6%;
            h3 {
                color: $bg-primary;
                margin-bottom: 2.9rem;
                margin-top: 4rem;
            }
            .span-text-black {
                font-weight: 600;
            }
        }
        .right {
            width: 100%;
            max-width: 63%;
            padding-top: 3.5rem;
            .swiper {
                .swiper-wrapper {
                    width: 100%;
                    display: flex;
                    margin-bottom: 5rem;
                    margin-left: 2rem;
                    .swiper-slide {
                        width: 100%;
                        }
                    }
            }
            .bottom {
                width: 100%;
                padding-left: 2rem;
                .infos-cidade {
                    padding-bottom: 1rem;
                    animation: Spawn 1s ease forwards;
                    .content {
                        display: flex;
                        align-items: end;
                        gap: 4rem;
                        .image {
                            width: 100%;
                            max-width: 34.1rem;
                            height: 27.2rem;
                            border-radius: 10px;
                            background: #D9D9D9;
                            overflow: hidden;
                            img {
                                width: 100%;
                                height: 100%;
                                object-fit: cover;
                            }
                        }
                        .infos {
                            h3 {
                                color: $green-dark;
                                margin-top: 6px;
                                margin-bottom: 2.5rem
                            }
                            p {
                                margin-bottom: 5px;
                            }
                            .link-maps {
                                display: flex;
                                align-items: center;
                                gap: 9px;
                                margin-top: 1.9rem;
                                i {
                                    font-size: 1.8rem;
                                }
                            }
                        }
                    }
                    &.camargo {
                        display: none;
                    }
                    &.cascavel {
                        display: none;
                    }
    
                    &.general {
                        display: none;
                    }
                }
             
            }
        }
    }
    @include responsive(1130px) {
        .container {
            .left {
                .subtitulo {
                    .span-subtitulo-2 {
                        font-size: 14px;
                    }
                }
                h3 {
                    font-size: 28px;
                }
                .text-02 {
                    font-size: 16px;
                }
            }
            .right {
                .navegacao {
                    li {
                        .btn-cidades {
                            max-width: 15.6rem;
                            font-size: 13px;
                        }
                    }
                }
            }
        }
    }
    @include responsive(1000px) {
        .container {
            flex-direction: column;
            .left {
                width: 100%;
                max-width: 100%;
                margin-bottom: 5rem;
                h3 {
                    font-size: 30px;
                }
            }
            .right {
                max-width: 100%;
                .navegacao {
                    li {
                        .btn-cidades {
                            max-width: 90.6%;
                        }
                    }
                }
                .bottom {
                    .infos-cidade {
                        .content {
                            .image {
                                max-width:  50.1%;
                            }
                        }
                    }
                }
            }
        }
    }
    @include responsive(600px) {
        .container {
            .left {
                text-align: center;
                .subtitulo {
                    justify-content: center
                }
            }
            .right {
                .navegacao {
                    row-gap: 1rem;
                    flex-wrap: wrap;
                    justify-content: start;
                    li {
                        max-width: 30%;
                        .btn-cidades {
                            font-size: 12px;
                        }
                    }
                }
                .bottom {
                    .infos-cidade {
                        .content {
                            .image {
                                max-width: 45.1%;
                                height: 22.2rem;
                            }
                            .infos {
                                .span-subtitulo-2 {
                                    font-size: 12px;
                                }
                                h3 {
                                    font-size: 25px;
                                    margin-bottom: 1.5rem;
                                }
                                .text-02 {
                                    font-size: 16px;
                                }
                                .span-maps {
                                    margin-top: 1rem;
                                    font-size: 16px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
    @include responsive(500px) {
        .container {
            .left {
                .subtitulo {
                    .linha {
                        display: none;
                    }
                }
            }
            .right {
                max-width: 100%;
                .bottom {
                    padding-left: 0rem;
                    .infos-cidade {
                        .content {
                            flex-direction: column-reverse;
                            align-items: center;
                            text-align: center;
                            .image {
                                max-width: 100%;
                            }
                            .infos {
                               .link-maps {
                                justify-content: center;
                               }
                            }
                        }
                    }
                }
            }
        }
    }
    @include responsive(400px) {
       .container {
        .right {
            .swiper {
                .swiper-wrapper {
                    .swiper-slide {
                        .btn-cidades {
                            font-size: 10px;
                        }
                    }
                }
            }
        }
       }
    }
}

