.s-slides-logos {
    padding-top: 35px;
    padding-bottom: 35px;
    .container {
        display: flex;
        align-items: center;
        .title-slides {
            padding-right: 3rem;
            position: relative;
            h4 {
                color: $color-green;
                font-size: 14px;
                font-weight: 500;
                text-transform: uppercase;
                letter-spacing: 2px;
            }
            span {
                font-weight: 600;
            }
            :after {
                content: "";
                display: block;
                width: 100%;
                max-width: 1px;
                height: 25px;
                left: 15rem;
                top: 5px;
                background-color: #17d46038;
                position: absolute;
            }
        }
        .logosSwiper {
            width: 100%;
            padding-left: 5rem;
            .swiper-wrapper {
                display: flex;
                align-items: center;
                .swiper-slide {
                    width: 100%;
                }
            }
        }
    }

    @include responsive(600px) {
        .container {
            flex-direction: column;
            align-items: start;
            .title-slides {
                margin-bottom: 3rem;
                padding-right: 0rem;
                width: 100%;
                text-align: center;
                :after {
                    display: none;
                }
            }
        }
    }
}



.subtitulo {
    width: 100%;
