    gap: 3.5rem;
    .linha {
            width: 100%;
            max-width: 72px;
            height: 1px;
            background: $green-dark-two;
    }
}

//Formulario de Contato//

.formulario {
    width: 100%;
    display: flex;
    justify-content: end;
    padding-top: 12rem;
    form {
        width: 100%;
        max-width: 100%;
        .column {
            width: 100%;
            max-width: 55.2rem;
            border: 1px solid $text-gray;
            padding-left: 3rem;
            padding-right: 3rem;
            padding-bottom: 2rem;
            padding-top: 2rem;
            margin-bottom: 2.6rem;
            border-radius: 24px;
            input {
                font-size: 16px;
                color: $text-gray;
                font-family: Inter;
                font-weight: 500;
                line-height: 120%; /* 19.2px */
                
            }
            select {
                font-size: 16px;
                color: #7e818a;
                font-family: Inter;
                font-weight: 500;
                line-height: 120%;
            }
        }

        .btn-formulario {
            display: flex;
            align-items: center;
            justify-content: center;
            background: $bg-primary;
            width: 48.7rem;
            height: 5.6rem;
            color: #FFF;
            font-family: Inter;
            font-size: 18px;
            font-style: normal;
            font-weight: 600;
            line-height: 120%; /* 21.6px */
            transition: all 0.5s ease-in;
            border-radius: 24px;
            cursor: pointer;
            &:hover {
                background: $green-dark-two;
            }
            p {
                width: 100%;
                height: 100%;
            }
            input {
                width: 100%;
                height: 100%;
                color: #FFF;
                font-family: Inter;
                font-size: 18px;
                font-weight: 600;
                line-height: 120%; /* 21.6px */
                background: transparent;
                cursor: pointer;
            }
            span {
                display: none;
            }
         
        }
        textarea{
            width: 100%;
            max-width: 48.7rem;
            height: 18.9rem;
            border: 1px solid $text-gray;
            font-size: 16px;
            color: $text-gray;
            font-family: Inter;
            font-weight: 500;
            line-height: 120%; /* 19.2px */
            padding-top: 1.4rem;
            padding-left: 2rem;
            margin-bottom: 1.4rem;
            border-radius: 24px;
        }
    }

    @include responsive(930px) {
        form {
            display: flex;
            flex-direction: column;
            align-items: end;
            .column {
                max-width: 38.2rem;
                input {
                    width: 100%;
                    font-size: 14px;
                }
            }
            textarea {
                max-width: 38.2rem;
                font-size: 13px;
            }
            .btn-formulario {
                width: 38.2rem;
                input {
                    font-size: 16px;
                }
            }
        }
    }
    @include responsive(750px) {
        form {
            .column {
                max-width: 32.2rem;
            }
            textarea {
                max-width: 32.2rem;
            }
            .btn-formulario {
                max-width: 32.2rem;
            }
        }
    }
    @include responsive(650px) {
        justify-content: center;
        #wpcf7-f15-p11-o1 {
            width: 100%;
        }
        form {
            align-items: center;
            .column {
                max-width: 100%;
            }
            textarea {
                max-width: 100%;
            }
            .btn-formulario {
                max-width: 100%;
            }
        }
    }
}


