/*PAGINA HOME*/
.s-hero { 
    padding-bottom: 8rem;
    background-color: $bg-primary;
    position: relative;
    overflow: hidden;
    .container {
        display: flex;
        justify-content: space-between;
        .left {
            width: 100%;
            max-width: 52.9rem;
            padding-top: 18.4rem;
            h1 {
                margin-bottom: 3.8rem;
            }
            .text-inicial {
                margin-bottom: 6.2rem;
            }
            .link-btn-hero {
                display: flex;
                width: 100%;
                max-width: 24rem;
                height: 10rem;
                .btn-primary {
                    margin-bottom: 4.8rem;
                }
            }   
            span {
                background: $gradient-span;
                background-clip: text;
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;
            }
        }
        .right {
            width: 100%;
            padding-top: 18.4rem;
            z-index: 1;
            .content {
                width: 100%;
                display: flex;
                justify-content: end;
                .image {
                    width: 100%;
                    max-width: 49rem;
                    height: 39rem;
                    display: flex;
                    justify-content: end;
                    overflow: hidden;
                    border-top-left-radius: 8rem;
                    border-bottom-right-radius: 8rem;
                    img {
                        width: 100%;
                        height: 100%;
                    }
                }
            }
            .icones-bottom {
             width: 100%;
             display: flex;
             justify-content: space-between;
             .en-plus {
                margin-top: -79rem;
                margin-left: -26rem;
             }
             .box-fsc {
                position: inherit;
                margin-top: -16rem;
                margin-right: 2rem;
             }
            }
            
        }
        .fundo-verde-claro {
            width: 45%;
            height: 100%;
            left: 55%;
            background-color: $green-dark;
            position: absolute;
        }
    }

    @include responsive(1670px) {
        .container {
            .right {
                .items {
                    margin-left: 44.5rem;
                }
            }
        }
    }
    @include responsive(1360px) {
        .container {
            .right {
                .items {
                    margin-left: 46.8rem;

                }
            }
        }
    }
    @include responsive(1250px) {
        .container {
            .left {
                h1 {
                    font-size: 37px;
                }
                p {
                    font-size: 14px;
                }
            }
            .right {
                .imagem-plantas {
                    width: 90%;
                }
                .items {
                    margin-left: 44.8rem;

                }
            }
        }
    }

    @include responsive(1100px) {
        .container {
            .right {
                .items {
                    margin-left: 42.8rem;
                }
            }
        }
    }
    @include responsive(1050px) {
        padding-bottom: 12rem;
        .container {
            display: flex;
            justify-content: center;
            text-align: center;
            .left {
                width: 100%;
                max-width: 70%;
                display: flex;
                flex-direction: column;
                align-items: center;
            }
            .right {
               display: none;
            }
            .fundo-verde-claro  {
                display: none;
            }
        }
    }
    @include responsive(715px) {
        .container {
            .left {
                width: 100%;
                max-width: 100%;
            }
        }
    }
    @include responsive(430px) {
        padding-left: 1rem;
        padding-right: 1rem;
        .container {
            .left {
                padding-top: 15.4rem;
                h1 {
                    font-size: 31px;
                    margin-bottom: 2.8rem;
                }
                .text-inicial {
                    margin-bottom: 3.2rem;
                }
            }
        }
    }
    @include responsive(360px) {
        .container {
            .left {
                h1 {
                    font-size: 30px;
                }
                p {
                    font-size: 13px;
                }
            }
        }
    }
}

.s-sobre-nos {
    width: 100%;
    position: relative;
    background: #fff;
    .container {
        display: flex;
        .left {
            width: 100%;
            max-width: 44%;
            .fundo {
                position: absolute;
                width: 100%;
                max-width: 94%;
                height: 100%;
                background-color: $secondary-color;
                left: -48%;
            }
            .image {
                width: 100%;
                display: flex;
                justify-content: start;
                align-items: center;
                padding-top: 7rem;
                padding-bottom: 4rem;
                img {
                    width: 100%;
                    max-width: 40rem;
                    z-index: 10;
                }
            }
        }
        .right {
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: end;
            .text {
                width: 100%;
                padding-left: 5.8rem;
                display: flex;
                flex-direction: column;
                justify-content: start;
                padding-bottom: 7.8rem;
                position: relative;
                .titulo-icone {
                    display: flex;
                    align-items: center;
                    h3 {
                        margin-top: 1.8rem;
                        margin-bottom: 4.5rem;
                        color: $bg-primary;
                    }
                }
                .txt {
                    display: flex;
                    gap: 3.5rem;
                    .texto-1 {
                        p {
                            margin-bottom: 1.8rem;
                            color: #7E818A;
                        }
                    }
                    .texto-2 {
                        p {
                            margin-bottom: 1.8rem;
                            color:#7E818A;
                        }
                    }
                }
                .link-btn-sobre-nos {
                    width: 100%;
                    max-width: 21rem;
                    button {
                        margin-top: 2.6rem;
                    }
                }
            }
        }
    }
    @include responsive(1360px) {
        .container {
            .right {
               .text {
                .span-subtitulo {
                    font-size: 13px;
                }
                .titulo-icone {
                    h3 {
                        font-size: 28px;
                    }
                }
               }
            }
        }
    }
    @include responsive(1150px) {
        .container {
            
            .right {
               .text {
                .span-subtitulo {
                    font-size: 12px;
                }
                .titulo-icone {
                    h3 {
                        font-size: 26px;
                    }
                }
                .txt {
                    .texto-1 {
                        p {
                            font-size: 15px;
                        }
                    }
                    .texto-2 {
                        p {
                            font-size: 15px;
                        }
                    }
                }
               }
            }
        }
    }
    @include responsive(1050px) {
        .container {
            flex-direction: column-reverse;
            align-items: center;
            text-align: center;
            .left {
                display: none;
            }
            .right {
                align-items: center;
                .text {
                    padding-left: 0rem;
                    justify-content: center;
                    align-items: center;
                    padding-top: 3.8rem;
                }
            }
        }
    }
    @include responsive(750px) {
        .container {
            .right {
                .text {
                    text-align: center;
                    align-items: center;
                    .txt {
                        display: flex;
                        flex-direction: column;
                    }
                }
            }
        }
    }
}

.s-nossos-produtos {
    position: relative;
    overflow: hidden;
    .container {
        display: flex;
        .left {
            display: flex;
            flex-direction: column;
            justify-content: start;
            width: 100%;
            max-width: 60%;
            padding-right: 26.8rem;
            padding-top: 7.7rem;
            padding-bottom: 7.5rem;
