                color: $bg-primary;
                margin-bottom: 4.2rem;
                margin-top: 1.8rem;
            }
            .textos {
                p {
                    color: $text-gray;
                    margin-bottom: 2rem;
                }
            }
            .link-btn-produtos {
                width: 100%;
                max-width: 24rem;
                button {
                    background-color: $green-dark-two;
                    max-width: 22rem;
