                width: 100%;
                max-width: 44%;
                height: 100%;
                position: absolute;
                background-color: #17AA61;
                padding-top: 5rem;
                img {
                    width: 100%;
                    max-width: 80%;
                    margin-left: 20%;
                }
            }
            .conteudo {
                display: flex;
                justify-content: center;
                width: 100%;
                height: 100%;
                z-index: 10;
                position: relative;
                .imagem-principal-nossos-produtos {
                    width: 100%;
                    max-width: 48rem;
                    height: 55rem;
                    overflow: hidden;
                    margin-left: -65%;
                    margin-top: 9rem;
                    border-top-left-radius: 8rem;
                    border-bottom-right-radius: 8rem;
                    .image-section-produtos {
                        width: 100%;
                        height: 100%;
                    }
                }
            }
        }
    }

    @include responsive(1150px) {
        .container {
            .left {
                .span-subtitulo {
                    font-size: 12px;
                }
                h3 {
                    font-size: 26px;
                }
                .textos {
                    p {
                        font-size: 15px;
                    }
                }
            }
        }
    }

    @include responsive(994px) {
        .container {
            .left {
                padding-right: 15.8rem;
            }
        }
    }

    @include responsive(800px) {
        .container {
            .left {
                text-align: center;
                align-items: center;
                max-width: 100%;
                padding-right: 0;
            }
            .right {
                display: none;
            }
        }
    }
}


