.s-linha-do-tempo {
    display: flex;
    align-items: start;
    flex-direction: column;
    width: 100%;
    background: rgba(23, 212, 96, 0.10);
    overflow: hidden;
    padding-top: 7.5rem;
    padding-bottom: 7.8rem;
    position: relative;
    .container {
        .content {
            width: 100%;
            display: flex;
            flex-direction: column;
            .slide-linha-do-tempo {
                width: 100%;
                animation: spawnSlideTimeline 2s ease;
                .conteudo-do-slide {
                    display: flex;
                    gap: 5rem;
                    align-items: center;
                    margin-bottom: 5.4rem;
                    .left {
                        width: 100%;
                        max-width: 38rem;
                        height: 32rem;
                        border-radius: 1rem;
                        background: #17AA61;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        flex-direction: column;
                        h3 {
                            color: #FFF;
                            text-align: center;
                            font-family: Inter;
                            font-size: 80px;
                            font-weight: 700;
                            line-height: 120%;
                        }
                        p {
                            color: #033;
                            text-align: center;
                            font-family: Inter;
                            font-size: 24px;
                            font-weight: 600;
                            line-height: 120%;
                        }
                    }
                    .center {
                       p {
                        color: #3F3F3F;
                        font-family: Inter;
                        font-size: 16px;
                        font-weight: 300;
                        line-height: 120%; 
                       }
                    }
                    .right {
                        width: 100%;
                        max-width: 19rem;
                        height: 14rem;
                    }
                }
            }
            .timelineButtons {
                width: 100%;
                height: 10rem;
                display: flex;
                justify-content: space-between;
            }
        }
    }
    .back-line {
        width: 100%;
        height: 2px;
        background: #c6d9cd;
        margin-top: -64px;
    }
    .line {
        transition: all 0.6s ease;
        width: 0;
        height: 2px;
        background: $color-green;
        margin-top: -2px;
    }

    @include responsive(1000px) {
        .container {
            .content {
                .slide-linha-do-tempo {
                    .conteudo-do-slide {
                        .left {
                            h3 {
                                font-size: 55px;
                            }
                            p {
                                font-size: 20px;
                            }
                        }
                    }
                }
            }
        }
    }
    @include responsive(500px) {
        .container {
            .content {
                .slide-linha-do-tempo {
                    .conteudo-do-slide {
                        flex-direction: column;
                        align-items: center;
                        text-align: center;
                    }
                }
            }
        }
        .back-line {
            margin-top: -71px;
        }
    }

}

