.s-ancoras {
    padding-top: 3.4rem;
    .container {
        padding-bottom: 3.4rem;
        border-bottom: 1px solid #f1f1f1;
        ul {
            width: 100%;
            display: flex;
            justify-content: space-between;
            gap: 2rem;
            li {
                position: relative;
                a {
                    font-size: 12px;
                    font-weight: 500;
                    text-transform: uppercase;
                    letter-spacing: 2px;
                    color: $text-gray;
                    transition: all 0.3s ease-in;
                    &:hover {
                        color: #17D460;
                        font-weight: 600;
                    }
                }
                ::after {
                    content: "";
                    display: block;
                    width: 100%;
                    max-width: 4px;
                    height: 4px;
                    border-radius: 100%;
                    left: -13px;
                    top: 6px;
                    background-color: #7E818A;
                    position: absolute;
                }
            }
        }
    }

    @include responsive(900px) {
        .container {
            ul {
                flex-wrap: wrap;
                li {
                    a {
                        font-size: 11px;
                    }
                }
            }
        }
    }

    @include responsive(500px) {
        .container {
            ul {
                justify-content: center;
                column-gap: 3rem;
                row-gap: 2rem;
            }
        }
    }
}

.s-sobre-pellets {
