.s-dicas-consumidor {
    width: 100%;
    position: relative;
    z-index: 1;
    background: rgba(23, 212, 96, 0.1);
    padding-top: 7rem;
    padding-bottom: 4.5rem;
    .container {
        .top {
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding-bottom: 3.6rem;
            .left {
                width: 100%;
                max-width: 60rem;
                h3 {
                    color: $bg-primary;
                    margin-top: 2rem;
                }
            }
            .right {
                width: 100%;
                max-width: 15rem;
                img {
                    width: 100%;
                }
            }
        }
        .lista-de-dicas {
            display: flex;
            align-items: center;
            flex-wrap: wrap;
            gap: 5rem;
            border-bottom: 1px solid #C3CFE5;
            padding-bottom: 4.5rem;
            li {
                width: 100%;
                max-width: 35rem;
                .icone-dica {
                    margin-bottom: 1.4rem;
                    i {
                        font-size: 3.4rem;
                    }
                }
                .title-style-dicas {
                    margin-bottom: 1.4rem;
                    color: #00292E;
                    font-family: Inter;
                    font-size: 17px;
                    font-weight: 600;
                    line-height: 120%;
                    letter-spacing: -0.5px;
                }
            }
        }
        .saiba-mais-dicas {
            padding-top: 3rem;
            display: flex;
            justify-content: space-between;
            align-items: center;
            p {
                color: #525B67;
                font-family: Inter;
                font-size: 14px;
                font-weight: 600;
            }
        }
    }

    @include responsive(1050px) {
        .container {
            .top {
                .left {
                    .subtitulo {
                        .span-subtitulo-2 {
                            font-size: 12px;
                        }
                    }
                    h3 {
                        font-size: 28px;
                    }
                    .span-titulos {
                        font-size: 28px;
                    }
                }
                .right {
                    display: none;
                }
            }
            .lista-de-dicas {
                li {
                    max-width: 25rem;
                }
            }
        }
    }
    @include responsive(600px) {
        .container {
            .top {
                .left {
                    text-align: center;
                    .subtitulo {
                        justify-content: center;
                    }
                }
            }
            .lista-de-dicas {
                li {
                    max-width: 100%;
                    text-align: center;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                }
            }
        }
    }

    @include responsive(400px) {
        .container {
            .top {
                .left {
                    .subtitulo {
                        .span-subtitulo-2 {
                            font-size: 10px;
                        }
                    } 
                    h3 {
                        font-size: 24px;
                    }
                    .span-titulos {
                        font-size: 24px;
                    }
                }
            }
        }
    }
}

