.s-vagas-de-trabalho {
    height: 100%;
    padding-top: 7.8rem;
    padding-bottom: 3rem;
    .container {
        .topo {
            .texto-vagas-topo {
                margin-bottom: 3rem;
            }
            .title-vagas-trabalho {
                margin-bottom: 3rem;
            }
            .lista-observações {
                display: flex;
                flex-direction: column;
                margin-bottom: 4rem;
                li {
                    list-style-type: disc;
                    .texto-observações {
                        color: #7E818A;
                        font-weight: 300;
                        margin-bottom: 7px;
                    }
                }
            }
            .listas-pre-requisitos {
                .accordion {
                    width: 100%;
                    margin-bottom: 1.5rem;
                    .contentBx {
                        position: relative;
                        .label {
                            display: flex;
                            align-items: center;
                            justify-content: space-between;
                            position: relative;
                            width: 100%;
                            height: 5.5rem;
                            border-radius: 10px;
                            border: 1px solid #F2F2F2;
                            background: #F1F1F1;
                            cursor: pointer;
                            padding-left: 2.3rem;
                            padding-right: 2.3rem;
                        }
                        .content {
                            position: relative;
                            padding-left: 10px;
                            padding-right: 10px;
                            padding-bottom: 0px;
                            padding-top: 0px;
                            background-color: #ffff;
                            height: 0;
                            overflow: hidden;
                            transition: 0.5s;
                            border-left: 1px solid  transparent;
                            border-right: 1px solid  transparent;
                            border-bottom: 1px solid  transparent;
                            border-bottom-left-radius: 2rem;
                            border-bottom-right-radius: 2rem;
                            padding-left: 2.3rem;
                            padding-right: 2.3rem;
                            .infos-requisitos {
                                display: flex;
                                flex-direction: column;
                                .text-requisitos {
                                    padding-top: 1rem;
                                    transition: all 0.3s ease-in;
                                }
                                .texto-vagas-topo {
                                    margin-bottom: 2rem;
                                }
                            }

                        }
                    }
                    .contentBx.active-accordeon {
                        height: 100%;
                        .content{
                            height: 32rem;
                            border-left: 1px solid  #F1F1F1;
                            border-right: 1px solid  #F1F1F1;
                            border-bottom: 1px solid  #F1F1F1;
                        }
                    }
                }
            }
        }
    }

    @include responsive(550px) {
        .container {
            .topo {
                text-align: center;
                .listas-pre-requisitos {
                    .accordion {
                        .contentBx {
                            .label {
                                .title-requisitos {
                                    font-size: 18px;
                                }
                            }
                            .content {
                                overflow: scroll;
                            
                                &::-webkit-scrollbar {
                                    width: 8px;
                                }

                                &::-webkit-scrollbar-thumb {
                                    background-color: #888; 
                                    border-radius: 6px; 
                                }

                                &::-webkit-scrollbar-track {
                                    background-color: #f1f1f1; 
                                    border-radius: 6px; 
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

.s-vagas-disponiveis {
    width: 100%;
    background-color: #F1F1F1;
    padding-top: 3.3rem;
    .container {
        .title-vagas-trabalho {
            margin-bottom: 7px;
        }
        .texto-vagas-topo {
            margin-bottom: 0rem;
        }
        .informações-vagas {
            width: 100%;
            display: flex;
            gap: 1rem;
            .left {
                    width: 100%;
                    max-width: 43%;
                    padding-top: 4rem;
                    .detalhes-vagas {
                        display: flex;
                        flex-direction: column;
                        overflow: hidden;
                        height: 100%;
                        .swiper-container {
                            height: 100vh; // Defina a altura desejada para os slides
                            position: relative;
                            .swiper-slide {
                                text-align: center;
                                display: flex;
                                align-items: baseline;
                                justify-content: center;                            
                                font-size: 24px;
                                .vaga {
                                    cursor: pointer;
                                    display: flex;
                                    flex-direction: column;
                                    align-items: start;
                                    width: 100%;
                                    max-width: 46.6rem;
                                    height: 24rem !important;
                                    border-radius: 2.4rem;
                                    background-color: #FFF;
                                    box-shadow: 0px 20px 30px 0px rgba(0, 0, 0, 0.10);
                                    margin-bottom: 2.8rem;
                                    padding: 2.7rem;
                                    transition: all 0.5s ease-in;
                                    &:hover {
                                        transform: scale(1.02);
                                        .span-detalhes-vaga {
                                            color: $green-dark-two;
                                        }
                                    }
                                    .span-detalhes-vaga {
                                        transition: all 0.5s ease-in;
                                    }
                                    .title-nome-vaga {
                                        margin-bottom: 2.2rem;
                                    }
                                    .id-do-post-01 {
                                        opacity: 0;
                                    }
                                    .infos {
                                        display: flex;
                                        align-items: center;
                                        gap: 5px;
                                        margin-bottom: 4px;
                                    }
                                    
                            }
                            .vaga.active-vaga {
                                transform: scale(1.050);
                                .span-detalhes-vaga {
                                    color: $color-green;
                                }
                            }
                        }
                        .swiper-pagination {
                            width: 100%;
                            max-width: 3%;
                            justify-content: center;
                            display: flex;
                            flex-direction: column;
                            align-items: start;
                            grid-row-gap: 3px;
                            height: 5rem;
                            .swiper-pagination-bullet-active {
                                background: #17AA61;
                            }
                        }
                    }
                }
            }
            .right {
                width: 100%;
                padding-top: 4rem;
                .cover-content {
                    animation: spawnVagas 0.8s ease-in-out forwards;
                    display: flex;
                    justify-content: end;
                    .detalhes-sobre-vaga {
                        display: flex;
                        flex-direction: column;
                        width: 100%;
                        height: 77rem;
                        border-radius: 16px;
                        border: 1px solid #F2F2F2;
                        background: #FFF;
                        box-shadow: 0px 10px 20px 0px rgba(0, 0, 0, 0.10);
                        padding-top: 3rem;
                        padding-bottom: 3rem;
                        padding-left: 4rem;
                        padding-right: 4rem;
                        .title-nome-vaga {
                            font-size: 20px;
                            margin-bottom: 2.8rem;
                        }
                        .span-vagas {
                            margin-top: 2rem;
                        }
                        .span-vagas-2 {
                            margin-left: 5px;
                        }
                        .link-candidatar-se {
                            width: 100%;
                        }
                        .id-do-post-02 {
                            opacity: 0;
                        }
                    }
                }

                .cover-content.others {
                   display: none;
                }
            }
        }
        .modal-vaga {
            display: none;
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 20;
            .overlay-vaga {
                display: flex;
                align-items: center;
                flex-direction: column;
                justify-content: center;
                position: fixed;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                z-index: 2;
                padding-top: 30px;
                .btn-close-modal-vaga {
                    max-width: 100%;
                    height: 100%;
                    position: fixed;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    background-color: rgba(0, 0, 0, 0.5);
                    z-index: 1000;
                    i {
                        font-size: 2.5rem;
                    }
                }
                .formulario-vaga {
                    width: 100%;
                    max-width: 56rem;
                    height: 80rem;
                    border-radius: 16px;
                    background: #FFF;
                    box-shadow: 0px 30px 60px 0px rgba(0, 0, 0, 0.25);
                    padding-top: 5.5rem;
                    padding-bottom: 9.4rem;
                    padding-left: 7rem;
                    padding-right: 7rem;
                    z-index: 2000;
                    .texto-topo-modal{
                        width: 100%;
                        display: flex;
                        flex-direction: column;
                        padding-bottom: 6.7rem;
                        .text-modal-vaga {
                            color: #000;
                            font-family: Inter;
                            font-size: 24px;
                            font-weight: 300;
                            line-height: 120%; 
                        }
                        .span-modal-vaga {
                            color: #000;
                            font-family: Inter;
                            font-size: 24px;
                            font-weight: 700;
                            line-height: 120%;
                        }
                    }
                    form {
                        display: flex;
                        flex-direction: column;
                    }
                    input {
                        color: #000;
                        font-family: Inter;
                        font-size: 16px;
                        font-weight: 500;
                        line-height: 120%;
                        padding-top: 2.4rem;
                        padding-bottom: 2.4rem;
                        padding-left: 3.4rem;
                        border-radius: 24px;
                        border: 1px solid #C6C8CC;
                        background: #FFF;
                        margin-bottom: 2.4rem;
                    }

                    .custom-file-upload {
                        display: flex;
                        justify-content: space-between;
                        align-items: center;
                        cursor: pointer;
                        padding-top: 2.2rem;
                        padding-bottom: 2.2rem;
                        padding-left: 3.4rem;
                        padding-right: 2.2rem;
                        border-radius: 24px;
                        border: 1px solid #C6C8CC;
                        background: #FFF;
                        margin-bottom: 2.4rem;
                        p {
                            font-family: Inter;
                            font-size: 16px;
                            font-weight: 500;
                            line-height: 120%;
                            color: #7F7F7F;
                        }
                        i {
                            font-size: 2.6rem;
                        }
                    }
                }
            }
        }
    }
    @include responsive(1366px) {
        padding-bottom: 8rem;
        .container {
            .informações-vagas {
                .left {
                    .detalhes-vagas  {
                            .swiper-container {
                                .swiper-wrapper {
                                .swiper-slide {
                                    margin-bottom: 30px !important;
                                }
                            }
                        }
                    }
                }
            }
            .modal-vaga {
                .overlay-vaga {
                    .formulario-vaga {
                        max-width: 47rem;
                        height: 55rem;
                        padding-top: 3.5rem;
                        .texto-topo-modal {
                            padding-bottom: 1.7rem;
                            .text-modal-vaga {
                                font-size: 20px;
                            }
                            .span-modal-vaga {
                                font-size: 20px;
                            }
                        }
                        input {
                            padding-top: 1.4rem;
                            padding-bottom: 1.4rem;
                            margin-bottom: 2.4rem;
                        }
                        .custom-file-upload {
                            padding-top: 1.2rem;
                            padding-bottom: 1.2rem;
                            margin-bottom: 2.4rem;
                        }
                        .btn-enviar-modal {
                            padding-top: 1.2rem;
                            padding-bottom: 1.2rem;
                        }
                    }
                }
            }
        }
    }
    @include responsive(1280px) {
        .container {
            .informações-vagas {
                .left {
                    .detalhes-vagas  {
                        .swiper-container {
                            .swiper-slide {
                                height: 24rem !important;
                            }
                        }
                    }
                }
            }
        }
    }
    @include responsive(1100px) {
        .container {
            .informações-vagas {
                .right {
                    display: flex;
                    flex-direction: column;
                    align-items: end;
                    .cover-content {
                        .detalhes-sobre-vaga {
                            max-width: 90%;
                        }
                    }
                }
            }
        }
    }
    @include responsive(830px) {
        text-align: center;
        padding-bottom: 8rem;
        .container {
            .informações-vagas {
                flex-direction: column;
                .left {
                    max-width: 100%;
                }
                .right {
                    align-items: center;
                    .cover-content {
                        justify-content: center;
                        .detalhes-sobre-vaga {
                            max-width: 75%;
                            text-align: start;
                            align-items: start;
                            height: 100%;
                        }
                    }
                }
            }
        }
    }
    @include responsive(768px) {
        .container {
            .informações-vagas {
                .left {
                        .detalhes-vagas {
                            .swiper-container {
                                .swiper-slide {
                                    .vaga {
                                        max-width: 80%;
                                        
                                }
                            }
                        }
                    }
                }
                .right {
                    .cover-content {
                        .detalhes-sobre-vaga {
                            max-width: 100%;
                            text-align: center;
                            align-items: center;
                        }
                    }
                }
            }    
        }
    }
    @include responsive(550px) {
        padding-top: 6rem;
        .container {
            .informações-vagas {
                .left {
                    .detalhes-vagas  {
                            .swiper-container {
                                .swiper-slide {
                                    margin-bottom: 10px !important;
                                    .vaga {
                                        align-items: center;
                                        max-width: 100%;
                                    }
                                }
                            }
                    }
                }
                .right {
                    .title-vagas-trabalho {
                        margin-bottom: 2rem;
                    }
                    .cover-content {
                        width: 100%;
                    }
                }
            }
        }
    }
    @include responsive(430px) {
        .container {
            .informações-vagas {
                .left {
                    .detalhes-vagas  {
                        overflow: unset;
                        .swiper-container {
                            .swiper-slide {
                                margin-bottom: 25px !important;
                                height: 24rem !important;
                            }
                        }
                    }
                }
                .right {
                    .cover-content {
                        .detalhes-sobre-vaga {
                            .span-vagas-2 {
                                margin-left: 5px;
                                margin-right: 5px;
                            }
                        }
                    }
                }
            }
        }
    }
    @include responsive(400px) {
        .container {
            .informações-vagas {
                .left {
                    height: 100%;
                    padding-bottom: 5rem;
                }
            }
        }
    }
}

